/* App.css */

:root {
  --first-color: #e7eaf6;
  --second-color: #a2a8d3;
  --third-color: #38598b;
  --fourth-color: #113f67;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: var(--first-color);
  color: var(--fourth-color);
}

.container {
  padding: 2rem;
}

.section-title {
  color: var(--third-color);
  margin-bottom: 1rem;
  font-size: 1.5rem;
  text-align: center;
}

.section-content {
  background-color: var(--second-color);
  padding: 1.5rem;
  border-radius: 8px;
}

.navbar {
  display: flex;
  justify-content: space-around;
  padding: 1rem;
  background-color: var(--third-color);
}

.navbar a {
  color: var(--first-color);
  text-decoration: none;
  font-weight: bold;
}

.footer {
  background-color: var(--third-color);
  color: var(--first-color);
  padding: 1rem;
  text-align: center;
}

/*-----------------------------------------------------------------------------------*/
/*Header section*/

/* Styling for the header container */
.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh; /* Takes up the entire first page */
  padding: 2rem;
  background-color: var(--first-color); /* Use your color palette */
}

/* Styling for the left text section */
.text-section {
  width: 50%; /* Takes half the width */
}

/* Greeting and name styling */
.greeting {
  font-size: 2rem;
  color: var(--fourth-color);
  margin: 0;
}

.name {
  font-size: 3rem;
  color: var(--third-color);
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 3px solid var(--third-color);
  animation: typing 3s steps(12, end), blink 0.75s step-end infinite;
}

.name span {
  display: inline-block;
  animation: typing-animation 3s steps(12, end);
}

/* Typing animation */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes typing-animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

/* Styling for the introductory paragraph */
.intro {
  margin-top: 1rem;
  color: var(--fourth-color);
  line-height: 1.6;
  font-size: 1.1rem;
}

/* Styling for the right photo section */
.photo-section {
  width: 40%; /* Takes the remaining width */
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-photo {
  width: 300px;
  height: 300px;
  border-radius: 50%; /* Circle effect */
  object-fit: cover;
  border: 5px solid var(--second-color);
}


