/* About.css */
/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.about-section {
  display: flex;
  height: 100vh; /* Full viewport height */
  overflow: hidden;
}

/* Left side with blue background and a larger photo */
.about-left {
  background-color: #38598b; /* Blue background */
  width: 50%; /* Takes half of the page width */
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

/* Enlarged, square photo with more styling */
.about-photo {
  width: 90%; /* Increased width to make the photo larger */
  max-width: 450px; /* Maximum size to maintain responsiveness */
  height: auto;
  aspect-ratio: 1/1; /* Ensures the image remains square */
  border-radius: 10px; /* Slight rounding for a polished look */
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.3);
  transition: transform 0.4s ease, box-shadow 0.4s ease;
  animation: zoomIn 1.2s ease forwards;
}

.about-photo:hover {
  transform: scale(1.08); /* Slight zoom effect on hover */
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.4);
}

/* Right side with animated text */
.about-right {
  width: 50%; /* Takes half of the page width */
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  color: #2d3e50;
  animation: fadeIn 1.5s ease forwards;
  opacity: 0;
}

.about-right h2 {
  font-size: 3rem;
  font-weight: 800;
  color: #113f67;
  margin-bottom: 1.5rem;
}

.about-right p {
  font-size: 1.4rem;
  line-height: 1.8;
  color: #485460;
  font-weight: 300;
}

/* Keyframes for animations */
@keyframes zoomIn {
  0% {
    transform: scale(0.9);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateX(40px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Additional Styling for a Polished Look */
.about-section {
  background-color: #f0f4f8;
  padding: 2rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
}

.about-left {
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}

.about-right {
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}
