/* Education Section */
/* Education Section */
.education-container {
  padding: 80px 20px;
  background: linear-gradient(135deg, #f0f4fc, #e7eaf6);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.education-title {
  text-align: center;
  font-size: 2.8em;
  color: #38598b;
  font-weight: bold;
  margin-bottom: 60px;
  letter-spacing: 1px;
}

/* Background decorations */
.education-container:before,
.education-container:after {
  content: '';
  position: absolute;
  border-radius: 50%;
  opacity: 0.15;
}

/* Existing Background Shapes */
.education-container:before {
  top: 10%;
  right: 5%;
  width: 100px;
  height: 100px;
  background: rgba(56, 89, 139, 0.3);
}

.education-container:after {
  bottom: 15%;
  left: 10%;
  width: 120px;
  height: 120px;
  background: rgba(17, 63, 103, 0.3);
}

/* Additional Background Decorations */
.education-container .background-decor-1,
.education-container .background-decor-2,
.education-container .background-decor-3 {
  content: '';
  position: absolute;
  border-radius: 50%;
  opacity: 0.1;
}

.education-container .background-decor-1 {
  top: 30%;
  left: -5%;
  width: 150px;
  height: 150px;
  background: radial-gradient(circle, rgba(56, 89, 139, 0.3), transparent 60%);
}

.education-container .background-decor-2 {
  bottom: 20%;
  right: -10%;
  width: 180px;
  height: 180px;
  background: radial-gradient(circle, rgba(17, 63, 103, 0.3), transparent 60%);
}

.education-container .background-decor-3 {
  top: 50%;
  right: 20%;
  width: 100px;
  height: 100px;
  background: radial-gradient(circle, rgba(100, 150, 200, 0.3), transparent 60%);
}

/* Timeline and Items */
.timeline {
  position: relative;
  max-width: 800px;
  width: 100%;
  padding: 20px 0;
}

.timeline:before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 100%;
  background: linear-gradient(to bottom, #113f67, #38598b); /* gradient line */
}

.timeline-item {
  position: relative;
  width: 50%;
  padding: 20px 40px;
  box-sizing: border-box;
  transition: transform 0.3s ease-in-out;
}

.timeline-item:hover {
  transform: scale(1.05);
}

.timeline-item.left {
  left: 0;
  text-align: right;
  padding-right: 50px;
}

.timeline-item.right {
  left: 50%;
  text-align: left;
  padding-left: 50px;
}

.timeline-item .timeline-content {
  position: relative;
  background: #ffffff;
  padding: 20px 30px;
  border-radius: 12px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  border-left: 5px solid #38598b;
  animation: fadeInUp 1.5s ease;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.timeline-item.right .timeline-content {
  border-left: none;
  border-right: 5px solid #38598b;
}

.timeline-item:hover .timeline-content {
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.timeline-item h3 {
  margin: 0;
  color: #113f67;
  font-size: 1.4em;
  font-weight: bold;
}

.timeline-item p {
  color: #38598b;
  font-size: 1.1em;
  margin-top: 5px;
}

.timeline-item .additional-info {
  font-size: 1em;
  color: #4a5a85;
  margin-top: 10px;
  font-style: italic;
  opacity: 0.9;
}

.timeline-item:before {
  content: '';
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 15px;
  height: 15px;
  background-color: #a2a8d3;
  border-radius: 50%;
  border: 3px solid #113f67;
  animation: bounce 1.5s infinite ease-in-out;
}

.timeline-item.left:before {
  left: auto;
  right: -8px;
}

.timeline-item.right:before {
  left: -8px;
}

/* Animations */
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/*-----------------------------------------------------------------------------------*/
/* Skills Section */
.skills-container {
  padding: 80px 20px;
  background: #f0f4fc; /* Match this to your color palette */
  text-align: center;
}

.skills-title {
  font-size: 2.8em;
  color: #38598b;
  font-weight: bold;
  margin-bottom: 10px;
}

.skills-subtitle {
  font-size: 1.2em;
  color: #4a5a85;
  margin-bottom: 40px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  margin-top: 20px;
}

.skill-item {
  background: #ffffff;
  border-radius: 8px;
  padding: 30px 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.skill-icon {
  font-size: 2.5em;
  color: #38598b; /* Main color for icons */
  margin-bottom: 15px;
}

.skill-item h3 {
  font-size: 1.4em;
  color: #113f67;
  margin-bottom: 10px;
}

.skill-item p {
  font-size: 1em;
  color: #4a5a85;
  line-height: 1.5;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
}

/*--------------------------------------------------------------------------------------*/
/* Projects Section */
.projects-container {
  padding: 80px 20px;
  background: #ffffff; /* Neutral background */
  text-align: center;
}

.projects-title {
  font-size: 2.8em;
  color: #38598b; /* Matches the main color palette */
  font-weight: bold;
  margin-bottom: 40px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
}

.project-card {
  background: #f0f4fc;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 15px;
}

.project-card h3 {
  font-size: 1.5em;
  color: #113f67; /* Slightly darker than title color */
  margin-bottom: 10px;
}

.project-card p {
  font-size: 1em;
  color: #4a5a85;
  margin-bottom: 15px;
  line-height: 1.5;
}

.project-btn {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1em;
  color: #ffffff;
  background: #38598b; /* Main button color */
  border-radius: 4px;
  text-decoration: none;
  transition: background 0.3s ease;
}

.project-btn:hover {
  background: #113f67; /* Darker shade for hover effect */
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }
}

/*------------------------------------------------------------------------------------*/
/*Achievements styling*/
/* Section Container */
.achievements-container {
  width: 90%;
  max-width: 1000px;
  margin: 80px auto;
  padding: 40px 30px;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  text-align: center;
  background-color: #e7eaf6;
  border-left: 5px solid #38598b;
}

/* Grid Layout for Cards (First and Second Row) */
.achievements-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); /* Adjust columns for first row */
  gap: 20px;
  justify-items: center; /* Ensure all items are centered horizontally */
  margin-top: 30px;
}

/* Individual Card Styling */
.achievement-card,
.research-card {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
  width: 100%;
  max-width: 320px; /* Ensures consistent card size */
  margin: 0 auto;
}

/* Headings */
.achievement-heading,
.research-heading {
  font-size: 1.3rem;
  font-weight: 600;
  color: #113f67;
  margin-bottom: 12px;
}

/* Description for Achievement and Research Cards */
.achievement-description,
.research-description {
  font-size: 1rem;
  color: #333;
  line-height: 1.4;
}

/* List Styling for Achievement Section */
.achievement-list {
  padding-left: 20px;
  list-style-type: none;
  font-size: 1rem;
  color: #38598b;
}

.achievement-list li {
  margin-bottom: 8px;
}

/* Icon Styling */
.achievement-icon,
.research-icon {
  font-size: 2rem;
  color: #38598b;
  margin-bottom: 12px;
}

/* Hover Effects for Cards */
.achievement-card:hover,
.research-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.achievement-card:hover .achievement-icon,
.research-card:hover .research-icon {
  transform: scale(1.1);
}

/* Additional Styling for Second Row */
.achievements-grid > .achievement-card:nth-child(4),
.achievements-grid > .research-card {
  justify-self: center; /* Ensure the cards in the second row are centered */
}

/* Responsive Design for Smaller Screens */
@media (max-width: 768px) {
  .achievements-container {
    width: 95%;
    padding: 30px;
  }

  .achievements-title {
    font-size: 1.8rem;
  }

  .achievement-heading,
  .research-heading {
    font-size: 1.2rem;
  }

  .achievement-description,
  .research-description {
    font-size: 0.95rem;
  }

  .achievement-list {
    font-size: 0.85rem;
  }

  .achievement-card,
  .research-card {
    padding: 15px;
  }

  .achievement-icon,
  .research-icon {
    font-size: 1.8rem;
  }
}

/*-------------------------------------------------------------------------------------*/
/*Certifications styling*/
/* Certifications Section */
.certifications-section {
  padding: 50px 20px;
  background: linear-gradient(180deg, #f0f4fc, #e6edf9);
  text-align: center;
}

.certifications-title {
  font-size: 2.4em;
  color: #38598b;
  font-weight: bold;
  margin-bottom: 30px;
  animation: fadeIn 1.2s ease-out;
}

.certifications-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

.certification-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px 20px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  animation: slideUp 1.2s ease-out;
}

.certification-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
  background: #eaf3fc;
}

.cert-icon {
  font-size: 2.5em;
  color: #38598b;
}

.cert-title {
  font-size: 1.1em;
  color: #4a5a85;
  text-align: left;
  line-height: 1.5;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .certifications-list {
    flex-direction: column;
    gap: 20px;
  }

  .certification-item {
    justify-content: center;
  }
}

