/*Contact section*/
/* Contact Section */
.contact-section {
  padding: 50px 20px;
  background: #f0f4fc;
  text-align: center;
}

.contact-title {
  font-size: 2.2em;
  color: #38598b;
  font-weight: bold;
  margin-bottom: 10px;
}

.contact-subtitle {
  font-size: 1em;
  color: #4a5a85;
  margin-bottom: 30px;
}

.contact-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  justify-items: center;
  padding: 0 20px;
}

.contact-card {
  background: #ffffff;
  border-radius: 8px;
  padding: 15px;
  width: 100%;
  max-width: 250px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.contact-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
  background: #eaf3fc;
}

.contact-icon {
  font-size: 2em;
  color: #38598b;
  margin-bottom: 10px;
}

.contact-card h3 {
  font-size: 1.2em;
  color: #113f67;
  margin-bottom: 8px;
}

.contact-link {
  font-size: 0.95em;
  color: #113f67;
  text-decoration: none;
}

.contact-link:hover {
  color: #4a5a85;
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .contact-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}

/*---------------------------------------------------------------------------------------*/
/*Navbar section*/
/* Navbar Styling */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  background-color: #113f67; /* Main dark blue background */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-logo {
  font-size: 1.5em;
  color: #f0f4fc; /* Light color for text/logo */
  font-weight: bold;
  text-transform: uppercase;
}

.navbar-links {
  display: flex;
  gap: 20px;
  list-style: none;
  margin: 0;
}

.navbar-links li {
  display: inline-block;
}

.navbar-links li a {
  text-decoration: none;
  font-size: 1em;
  color: #f0f4fc; /* Light text for links */
  transition: color 0.3s ease;
}

.navbar-links li a:hover {
  color: #a5b9f0; /* Soft blue for hover effect */
}

.navbar-contact-button {
  background-color: #7594e9; /* Coral for contact button */
  color: #f0f4fc; /* Light text on button */
  padding: 8px 20px;
  border-radius: 20px;
  font-size: 0.9em;
  font-weight: bold;
  text-decoration: none;
  transition: all 0.3s ease;
}

.navbar-contact-button:hover {
  background-color: #4b71ad; /* Darker coral on hover */
  box-shadow: 0 4px 10px rgba(72, 128, 233, 0.3); /* Coral shadow effect */
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .navbar-links {
    flex-direction: column;
    gap: 10px;
  }

  .navbar-contact-button {
    margin-top: 10px;
  }
}
