.footer {
  background-color: #38598b; /* Main color palette */
  color: #ffffff; /* White text for contrast */
  padding: 20px 0;
  text-align: center;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-text {
  font-size: 1.2em;
  margin-bottom: 10px;
  font-weight: normal;
}
